import {useLocation, useNavigate} from "react-router-dom";
import {useContext, useEffect, useState} from "react";
import TodoListContext from "../../../context/context";

import Logo from "../../Logo/Logo";
import Button from "../../Button/Button";
import Pagination from "../../Pagination/Pagination";
import TodoList from "../../TodoList/TodoList";
import SearchBar from "../../SearchBar/SearchBar";

import styles from "./TodosPage.module.scss";

const TodosPage = () => {
    const context = useContext(TodoListContext);
    const navigate = useNavigate();
    const {todoList, searchedTodoList} = context;
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const { search } = useLocation();

    const list = searchedTodoList.length ? searchedTodoList : todoList;

    useEffect(() => {
        const params = new URLSearchParams(search);
        const page = parseInt(params.get('page'));

        setTotalPages(Math.ceil(list.length / 5));

        if (page && page <= totalPages) {
            setCurrentPage(page);
        }

    }, [search, totalPages, list.length]);

    const startIndex = (currentPage - 1) * 5;
    const endIndex = startIndex + 5;
    const currentItems = list.slice(startIndex, endIndex);

    return (
        <div className={styles.todosPage}>
            <div className={styles.todosPageHeader}>
                <Logo />

                <div className={styles.todosPageSearchBar}>
                    <SearchBar />
                </div>


                <div className={styles.todosPageNewTodoButton}>
                    <Button
                        buttonType="button"
                        buttonStyle="primary"
                        handleClick={() => navigate("/react-todo-revva/todos/new")}
                    >
                        Create new
                    </Button>
                </div>
            </div>

            {list?.length > 0 ? <TodoList todoList={currentItems} /> : <div className={styles.todosPageNotFound}>Not found todos!!!</div>}

            {list?.length > 5 ? <Pagination currentPage={currentPage} totalPages={totalPages} /> : null}
        </div>
    );
};

export default TodosPage;
